import {Button, Container, Grid, Table, TableCell, TableRow, Typography} from "@mui/material";

import snapshot_cns from "../snapshot/cns.json";
import snapshot_cnsone from "../snapshot/cnsone.json";
import {useSelector} from "react-redux";
import Web3 from "web3";
import ceus_abi from '../abis/ceus_abi.json';
import {makeBatchRequest} from "../utils/promisify";
import {useEffect, useState} from "react";

const ceus_holders = {};

const cns_holders = {};
snapshot_cns.result.map(account => {
    cns_holders[account.address] = (account.value);
    ceus_holders[account.address] = (account.value);
})

const cnsone_holders = {};
snapshot_cnsone.result.map(account => {
    if (!ceus_holders[account.address]) {
        ceus_holders[account.address] = 0;
    }
    cnsone_holders[account.address] = (account.value);

    ceus_holders[account.address] = parseInt(ceus_holders[account.address]) + (parseInt(account.value));
})

