export default function Migration() {

    const {wallet} = useSelector(state => state.coineus);

    const [ceus, setCeus] = useState({});

    useEffect(()=>{
        getCEUSBals();
    }, [])

    const getCEUSBals = () => {
        const web3 = new Web3('https://rpc.fuse.io/');
        const contract = new web3.eth.Contract(ceus_abi, "0x4e69Ae0CD024754655b4eF74F24A8DCB39Ba07e8");

        const calls = [];
        Object.entries(ceus_holders).map(account => {

            if ((account[1] / (10 ** 18)) < 1) {
                return '';
            }

            calls.push(contract.methods.balanceOf(account[0]).call)
        })

        makeBatchRequest(web3, calls).then(resp => {
            let test = {};
            let index = 0;
            Object.entries(ceus_holders).map(account => {

                if ((account[1] / (10 ** 18)) < 1) {
                    return '';
                }

                test[account[0]] = resp[index];
                index++;
            })

            setCeus(test);
        });
    }

    const migrate = (account, amount) => {
        const web3 = new Web3(window.provider);
        const contract = new web3.eth.Contract(ceus_abi, "0x4e69Ae0CD024754655b4eF74F24A8DCB39Ba07e8")

        contract
            .methods
            .transfer(account, web3.utils.toWei(amount.toString()))
            .send({from: wallet.address, gasPrice: 10000000000})
            .once('transactionHash', function (tx) {
                console.log(tx);
            })
            .on('error', function (error) {
                console.log(error);
            })
            .then(function (receipt) {
                getCEUSBals();
                console.log(receipt)
            });
    }

    return (<Container>
            <Grid container spacing={2} style={{marginTop: -5, paddingBottom: 20}}>
                <Table>
                    {Object.entries(ceus_holders).map(account => {
                        return <TableRow>
                            <TableCell>{maskAddress(account[0])}</TableCell>
                            <TableCell>{account[1] / (10 ** 18)}</TableCell>
                            <TableCell>{ceus[account[0]] / (10 ** 18)}</TableCell>
                            <TableCell>
                                <Button
                                    onClick={() => {
                                        migrate(account[0], account[1] / (10 ** 18))
                                    }}
                                >SEND</Button>
                            </TableCell>
                        </TableRow>
                    })}
                </Table>
            </Grid>
        </Container>
    )
}

function maskAddress(address) {

    switch (address) {
        case '0xb80267ea7fa368374ee4d4bf10044778232adefe':
            return 'Divvy Fund'
        case '0x09501f8ca9476d0c516e121fb4dce4b7b2301d51':
            return 'Coineus Fund'
        case '0x53e2033f86c1a54bebae893dc8c9fae179b3faa2':
            return 'Validator'
        case '0x7042807d547a2de0720421a6d2f629c9eb43e73d':
            return 'CNSONE Contract'
        case '0xefa4222fd80328558801fe78ba3392048432d891':
            return 'CNSfuse'
        case '0x836c205c49d9db143f0a79dd384d08895f171c5f':
            return 'CNSvolt'
        case '0xf78116881138a2793e5820997cabf7268b6099ea':
            return 'CNSbtc'
        case '0x5c1bb0b286b5ba67b776f596aca6625baf9ccdd5':
            return 'CNSeth'
        case '0x5351b7eb69d81731f8c7a5948c1c0570fbd82f48':
            return 'CNSbnb'
        case '0x15c5c2cdb2676541a2f38919b7a82656a6c71f4f':
            return 'Storage'
        case '0x000000000000000000000000000000000000dead':
            return 'DEAD';
        case '0xc2e299b47398963c618de5b05c6bdecd4cc64022':
            return 'CNS Contract';
        case '0x0f2e60b799f2237633754f8eec7970c125e15d75':
            return 'Voltage LP'
        default:
            return address
    }

}
